<template>
  <div class="confirm-order">
    <van-nav-bar
      title="确认订单"
      left-text="返回"
      left-arrow
      fixed
      @click-left="back"
    />

    <div class="order-box">
      <div class="select-address">
        <div class="select-title">
          <div @click="pop">选择地址</div>
          <div><van-icon name="arrow" /></div>
        </div>
        <div class="user-box">
          <div class="user-info">
            <div class="user-name">{{ selectAddress.name }}</div>
            <div class="user-phone">{{ selectAddress.tel }}</div>
            <div class="user-default" v-show="selectAddress.isDefault">
              <van-tag mark color="#eb2403">默认</van-tag>
            </div>
          </div>
          <div class="user-address">{{ selectAddress.address }}</div>
        </div>
      </div>

      <div class="order-content">
        <div class="o-title">订单信息</div>
        <div>
          <div
            class="o-pro-item"
            v-for="(item, index) in productData"
            :key="index"
          >
            <ProList class="not-padding" :isHasCheckbox="false" :proData="item">
              <div class="o-count">x{{ item.count }}</div>
            </ProList>
          </div>
        </div>
        <div class="o-bottom">
          <div class="empty-round">
            <div class="round-box left-r"></div>
            <div class="round-box right-r"></div>
          </div>
          <div class="o-count-price">
            <div class="o-c1">共计 {{ total }} 件</div>
            <div class="o-c2">
              总价: <span>&yen;{{ money }}</span>
            </div>
          </div>
        </div>
      </div>

      <div class="pay">
        <van-button color="#eb2403" block round @click="pay"
          >立即结算</van-button
        >
      </div>
    </div>

    <!-- 选择地址列表 -->
    <van-popup v-model="show" position="bottom" closeable>
      <div class="address-list">
        <van-address-list
          v-model="chosenAddressId"
          :list="list"
          default-tag-text="默认"
          @select="select"
          @add="goNewAddress"
          @edit="goNewAddress"
        />
      </div>
    </van-popup>

    <!-- 密码键盘弹出层 -->
    <van-popup v-model="showPay" position="bottom" closeable>
      <!-- 密码框 -->
      <div class="pay-password">
        <div class="passowrd-title">支付密码</div>
        <van-password-input :value="payPassword" :focused="showKeyboard" />
        <!-- 数字键盘 -->
        <van-number-keyboard
          v-model="payPassword"
          :show="showKeyboard"
          @input="watchNumber"
        />
      </div>
    </van-popup>
  </div>
</template>

<script>
import ProList from "../components/ProList.vue";
export default {
  name: "ConfirmOrder",
  components: {
    ProList,
  },

  data() {
    return {
      show: false,
      //是否显示密码弹出层
      showPay: false,
      //是否显示数字键盘
      showKeyboard: true,
      chosenAddressId: "",
      list: [],

      //支付密码
      payPassword: "",

      //选择地址, (默认选择默认地址)
      selectAddress: {},

      sids: [],

      //需要购买商品数据
      productData: [],

      //订单商品总共数量
      total: 0,

      //订单总金额
      money: 0,

      //用户的支付密码
      userPayPassword: "123456",
      pone:"",
      arr:[]
    };
  },

  created() {
    //截取sids
    this.sids = this.$route.query.sids.split("-");
    // console.log(this.sids);
    let sids = JSON.stringify(this.sids);
    // console.log("sids====>",sids);

    this.getProductBySids(sids);
    // console.log(this.p1);
    // this.pone = this.$route.query.pone;
    // this.arr.push(this.pone);
    // // 数组
    // let arr = JSON.stringify(this.arr);
    // console.log("==========>",arr);
    // this.getProductBySids(arr);
    // this.getProductBySids(p1);

    this.getUserAddressData();
  },

  methods: {
    //返回上一级
    back() {
      this.$router.go(-1);
    },

    //获取用户地址数据
    getUserAddressData() {
      //获取token
      let tokenString = this.$cookies.get("TOKEN");

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          tokenString,
        },
      })
        .then((res) => {
          console.log("getUserAddressData res ==> ", res);

          if (res.data.code == 700) {
            //用户未登录, 拦截操作, 通知用户去登录
            this.$toast("请先登录");
            setTimeout(() => {
              this.$router.push({ name: "Login" });
            }, 2000);

            return;
          }

          if (res.data.code === 20000) {
            res.data.result.forEach((v) => {
              v.isDefault = Boolean(v.isDefault);
              v.address = `${v.province}${v.city}${v.county}${v.addressDetail}`;

              //选择默认地址
              if (v.isDefault) {
                this.selectAddress = { ...v };
                this.chosenAddressId = v.id;
              }
            });

            this.list = res.data.result;

            console.log("this.selectAddress ==> ", this.selectAddress);
          }
        })
        .catch((err) => {
          console.log("err ==> ", err);
        });
    },

    //弹出选择地址列表层
    pop() {
      this.show = true;
    },

    //选择地址
    select(item) {
      if (this.chosenAddressId === item.id) {
        return;
      }

      this.chosenAddressId = item.id;

      this.selectAddress = { ...item };

      this.show = false;
    },

    //跳转到新增地址或者编辑地址页面
    goNewAddress(item) {
      if (item) {
        this.$router.push({ name: "NewAddress", params: { aid: item.aid } });
      } else {
        this.$router.push({ name: "NewAddress" });
      }
    },

    //根据sids查询商品数据
    getProductBySids(sids) {
      //获取token
      let tokenString = this.$cookies.get("TOKEN");

      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          tokenString,
          sids,
        },
      })
        .then((res) => {
          console.log("getProductBySids res ==> ", res);

          if (res.data.code == 700) {
            //用户未登录, 拦截操作, 通知用户去登录
            this.$toast("请先登录");
            setTimeout(() => {
              this.$router.push({ name: "Login" });
            }, 2000);

            return;
          }

         

          if (res.data.code === 50000) {

            if (res.data.result.length === 0) {
              //如果没有商品, 默认跳转到购物袋页面
              return this.$router.push({name: 'Shopbag'});
            }

            let total = 0;
            let money = 0;

            res.data.result.forEach((v) => {
              total += v.count;
              money += v.count * v.price;
            });

            this.total = total;
            this.money = money.toFixed(2);
            this.productData = res.data.result;
          }
        })
        .catch((err) => {
          console.log("err ==> ", err);
        });
    },

    //结算订单
    pay() {
      //判断是否选择地址
      if (!this.chosenAddressId) {
        this.$toast("请选择地址");
        return;
      }

      //用户二次确定订单
      this.$dialog
        .confirm({
          title: "确认订单",
          message: "是否确认提交？",
        })
        .then(() => {
          //确定

          this.showPay = true;
        })
        .catch((err) => {
          //取消
          console.log("err ==> ", err);
        });
    },

    //监听数字键盘输入
    watchNumber(value) {
      let inputPassword = this.payPassword + value;
      console.log("inputPassword ==> ", inputPassword);

      if (inputPassword.length === 6) {
        //如果密码正确
        if (inputPassword === this.userPayPassword) {
          this.showPay = false;

          //发起结算订单请求
          console.log("发起结算订单请求");

          let tokenString = this.$cookies.get("TOKEN");

          let sids = JSON.stringify(this.sids);

          //获取手机号、收货人、地址、token、sids
          let data = {
            phone: this.selectAddress.tel,
            receiver: this.selectAddress.name,
            address: this.selectAddress.address,
            tokenString,
            sids,
          };

          this.axios({
            method: "POST",
            url: "/pay",
            data
          })
            .then((res) => {
              console.log("pay res ==> ", res);

              if (res.data.code == 700) {
                //用户未登录, 拦截操作, 通知用户去登录
                this.$toast("请先登录");
                setTimeout(() => {
                  this.$router.push({ name: "Login" });
                }, 2000);

                return;
              }

              this.$toast(res.data.msg)

              if (res.data.code === 60000) {

                //修改购物袋数量
                this.$root.publicShopbagCount -= this.total;

                setTimeout(() => {
                  this.$router.push({name: 'Order'});
                }, 2000)
              }

              
            })
            .catch((err) => {
              console.log("err ==> ", err);
            });
        } else {
          //提示密码错误
          this.$toast("支付密码错误");
        }
      }
    },
  },
};
</script>

<style lang="less" scoped>
.confirm-order {
  padding-top: 46px;
  padding-bottom: 54px;

  .passowrd-title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 16px;
    font-weight: bold;
    color: #666;
  }
  .pay-password {
    padding: 20px 10px 254px;
  }

  .address-list {
    padding: 15px 10px 10px;
  }

  /deep/ .van-address-list {
    padding: 15px 0 60px;
  }

  /deep/ .van-address-item {
    padding: 10px 0;
  }

  /deep/ .van-nav-bar .van-icon {
    color: #eb2403;
  }

  /deep/ .van-nav-bar__text {
    color: #eb2403;
  }

  /deep/ .van-address-item .van-radio__icon--checked .van-icon {
    border-color: #eb2403;
    background-color: #eb2403;
  }

  /deep/ .van-tag--danger {
    background-color: #eb2403;
  }

  /deep/ .van-button--danger {
    border-color: #eb2403;
    background-color: #eb2403;
  }

  .pay {
    position: fixed;
    left: 0;
    bottom: 10px;
    right: 0;
    padding: 0 10px;
  }

  .o-count-price {
    display: flex;
    align-items: center;
  }

  .o-c1 {
    margin-right: auto;
    font-size: 14px;
    color: #666;
  }

  .o-c2 {
    font-size: 14px;
    color: #e4393c;
    font-weight: bold;
  }

  .round-box {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: #f7f7f7;
    position: relative;
  }

  .left-r {
    left: -18px;
    margin-right: auto;
  }

  .right-r {
    right: -18px;
  }

  .o-bottom {
    padding: 10px 0 0;
    margin-top: 20px;
    border-top: 1px dashed #e8e8e8;
    position: relative;
  }

  .empty-round {
    position: absolute;
    left: 0;
    top: -8px;
    height: 16px;
    width: 100%;
    display: flex;
  }

  .o-pro-item {
    margin-bottom: 10px;
  }

  .not-padding {
    padding: 0;
  }

  .o-count {
    color: #666;
    font-size: 14px;
  }

  .o-title {
    padding-bottom: 10px;
    font-size: 14px;
  }

  .order-content {
    margin-top: 10px;
    padding: 10px;
    background-color: #fff;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }

  .select-title {
    display: flex;
  }

  /deep/ .van-icon-arrow {
    top: 3px;
  }

  .user-address {
    color: #666;
    margin-top: 5px;
  }

  .user-name {
    font-weight: bold;
    color: #eb2403;
  }

  .user-phone {
    margin: 0 10px;
  }

  .user-box {
    margin-top: 10px;
  }

  .user-info {
    display: flex;
    align-items: center;
  }

  .order-box {
    padding: 10px;
  }

  .select-address {
    padding: 10px;
    background-color: #fff;
    font-size: 14px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }

  /deep/ .van-nav-bar .van-icon {
    color: #eb2403;
  }

  /deep/ .van-nav-bar__text {
    color: #eb2403;
  }
}
</style>